import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Hireme from "../views/HireMe.vue";
import ContactMe from "../views/Contact.vue";
import About from "../views/About.vue";
// import MyStory from "../views/About/my_Story.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/contactme",
    name: "ContactMe",
    component:ContactMe
  },
  {
    path: "/hireme",
    name: "Hireme",
    component: Hireme,
  },
  {
    path: "/aboutMe",
    name: "About",
  component:About
  },

];

const router = new VueRouter({
  mode: "history",
  base:"/resume/",
  routes,
});

export default router;
